html, body {
  background-color: #fafafa;
  color: #444;
  height: 100%;
  margin: 0;
  padding: 0;
}

.app {
  padding-top: 60px;
}

.player-name {
  font-size: 2rem;
  text-align: right;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;;
  padding: 2rem;
}

.player-name-col:last-child .player-name {
  text-align: left;
}
.player-name-col:only-child .player-name {
  text-align: center;
}

.deck {
  margin-bottom: 10px;
}

.deck.card {
  border-radius: 2px;
  border: 2px solid #ccc;
  text-align: center;
}

.deck.card.ban {
  opacity: 0.6;
  border: 2px solid	#e6b8af;
  background-color: #fefefe;
}

.deck.card.ban .deck-status {
  background-color: #f4cccc;
}

.deck.card.safe {
  border: 2px solid	#b6d7a8;
}

.deck.card.safe .deck-status {
  background-color: #d9ead3;
}

.deck.card.steal {
  border: 2px solid	#b4a7d6;
}

.deck.card.steal.preliminary {
  border-style: dashed;
}

.deck.card.steal .deck-status {
  background-color: #d9d2e9;
}

.row.matchup .col:first-child .deck > .row {
  flex-direction: row-reverse;
}

.deck-actions__game-number::before {
  content: "G ";
}

.deck-details {
  display: flex;
  flex-direction: column;
  justify-content: center
}

.deck-details .card-title {
  font-size: larger;
}

.deck-houses {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
  background-color: #eee;
}

.deck-actions {
  background-color: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

ul.deck-links {
  list-style-type: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

ul.deck-links li:not(:last-child) {
  margin-right: 10px;
}

.house-icon {
  width: 2.5rem;
}

.deck-status {
  display: flex;
  justify-content: center;
  border-radius: 2px;
  background-color: #ddd;
}

.deck-select-button {
  border: none;
  margin: 5px;
  flex: 1;
}

.deck-status--label,
.deck-select-button--label {
  writing-mode: vertical-lr;
  text-orientation: upright;
  text-transform: uppercase;
  font-size: smaller;
  font-weight: bold;
}



.deck-input-textarea {
  height: 150px;
}

.btn + .btn {
  margin-left: 3px;
}

.alert-header {
  font-weight: bold;
  display: block;
}

.footer-wrapper {
  align-items: flex-end;
}

ul.footer {
  margin-top: 20px;
  padding: 20px 0 0;
  border-top: 1px solid #eee;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}